define('affinity-engine-web/transitions', ['exports'], function (exports) {
  exports['default'] = function () {
    this.transition(this.toRoute(function () {
      return true;
    }), this.use('toRight'));

    this.transition(this.toRoute(function (routeName) {
      return (routeName.match(/\./g) || []).length === 0 || routeName === 'index';
    }), this.use('toDown'));
  };
});